import { createRouteHandlerClient } from '@supabase/auth-helpers-nextjs'
import { cookies } from 'next/headers'
import { NextResponse } from 'next/server'
import { generateRandomString } from '@/app/lib/utils'

export async function POST(request: Request) {
  try {
    console.log('Verify code API called')
    const { email, code } = await request.json()
    console.log('Email received:', email)

    const supabase = createRouteHandlerClient({ cookies })

    // First check if the code exists and is valid
    console.log('Checking if code exists and is valid...')
    const { data: codeData, error: codeError } = await supabase
      .from('email_codes')
      .select('*')
      .eq('email', email)
      .eq('code', code)
      .eq('used', false)
      .gt('expires_at', new Date().toISOString())
      .single()

    if (codeError) {
      console.error('Error checking code:', codeError)
      return NextResponse.json(
        { error: 'Invalid or expired code' },
        { status: 400 }
      )
    }

    if (!codeData) {
      console.log('Code not found or already used')
      return NextResponse.json(
        { error: 'Invalid or expired code' },
        { status: 400 }
      )
    }

    // Mark the code as used
    console.log('Marking code as used...')
    const { error: updateError } = await supabase
      .from('email_codes')
      .update({ used: true })
      .eq('id', codeData.id)

    if (updateError) {
      console.error('Error marking code as used:', updateError)
      return NextResponse.json(
        { error: 'Failed to verify code' },
        { status: 500 }
      )
    }

    // Generate a secure temporary password
    const tempPassword = generateRandomString(32)

    // Create or update the user with the temporary password
    console.log('Creating/updating user...')
    const { data: { user }, error: userError } = await supabase.auth.admin.createUser({
      email,
      password: tempPassword,
      email_confirm: true
    })

    if (userError) {
      console.error('Error creating/updating user:', userError)
      return NextResponse.json(
        { error: 'Failed to create user account' },
        { status: 500 }
      )
    }

    // Sign in with the temporary password
    console.log('Signing in...')
    const { data: { session }, error: signInError } = await supabase.auth.signInWithPassword({
      email,
      password: tempPassword
    })

    if (signInError) {
      console.error('Error signing in:', signInError)
      return NextResponse.json(
        { error: 'Failed to sign in' },
        { status: 500 }
      )
    }

    if (!session) {
      console.error('No session returned')
      return NextResponse.json(
        { error: 'Failed to create session' },
        { status: 500 }
      )
    }

    console.log('User signed in successfully')

    // Set the session cookie
    const response = NextResponse.json({ 
      success: true,
      message: 'Code verified successfully'
    })
    response.cookies.set('sb-access-token', session.access_token, {
      path: '/',
      httpOnly: true,
      secure: process.env.NODE_ENV === 'production',
      sameSite: 'lax',
    })
    response.cookies.set('sb-refresh-token', session.refresh_token, {
      path: '/',
      httpOnly: true,
      secure: process.env.NODE_ENV === 'production',
      sameSite: 'lax',
    })
    return response
  } catch (error) {
    console.error('Error in verify-code API:', error)
    if (error instanceof Error) {
      console.error('Error details:', {
        name: error.name,
        message: error.message,
        stack: error.stack
      })
    }
    return NextResponse.json(
      { error: error instanceof Error ? error.message : 'Failed to verify code' },
      { status: 500 }
    )
  }
} 